:root {
  --colorhunt-dark-1: #D4ECDD;
  --colorhunt-dark-2: #345B63;
  --colorhunt-dark-3: #152D35;
  --colorhunt-dark-4: #112031;
  --colorhunt-dark-text: #a1acbd;
  --colorhunt-light-1: #FEF7DC;
  --colorhunt-light-2: #E6DDC6;
  --colorhunt-light-3: #C2B8A3;
  --colorhunt-light-4: #A19882;
  --font-ibm: "IBM Plex Sans Arabic", sans-serif;
  --weight-light-300: 300;
  --weight-regular-400: 400;
  --weight-bold-700: 700;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  min-height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: var(--font-ibm);
}

/*
Explicitly define a Segoe UI font-family so that we can assign Segoe UI 
Semilight to an appropriate font-weight.
*/
@font-face {
  font-family: "Segoe UI";
  font-weight: 200;
  src: local("Segoe UI Light");
}
@font-face {
  font-family: "Segoe UI";
  font-weight: 300;
  src: local("Segoe UI Semilight");
}
@font-face {
  font-family: "Segoe UI";
  font-weight: 400;
  src: local("Segoe UI");
}
@font-face {
  font-family: "Segoe UI";
  font-weight: 600;
  src: local("Segoe UI Semibold");
}
@font-face {
  font-family: "Segoe UI";
  font-weight: 700;
  src: local("Segoe UI Bold");
}
@font-face {
  font-family: "Segoe UI";
  font-style: italic;
  font-weight: 400;
  src: local("Segoe UI Italic");
}
@font-face {
  font-family: "Segoe UI";
  font-style: italic;
  font-weight: 700;
  src: local("Segoe UI Bold Italic");
}
body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  height: 100vh;
  scroll-behavior: smooth;
}

h1, h2, h3, h4, p {
  margin-bottom: 0;
  line-height: 1.1;
  margin-top: 0;
}

a, a:link, a:visited, a:active {
  text-decoration: none !important;
}

ul {
  list-style-type: none;
  padding: 0;
}

section {
  height: 100vh;
  scroll-snap-align: start;
  position: relative;
}

.navigation {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 5rem;
  background: transparent;
  padding: 0.25em 0.5em 0.25em 0.5em;
  width: 100%;
  z-index: 2;
}
.navigation__list {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}
.navigation__item {
  color: var(--colorhunt-dark-1);
  font-size: 1.1rem;
}
.navigation__link {
  color: var(--colorhunt-dark-1);
  padding: 0.4em;
  border-radius: 1rem;
  border: 1px solid transparent;
  background-color: transparent;
  transition: all 0.4s ease;
}
.navigation__link:hover {
  border: 1px solid transparent;
  background-color: var(--colorhunt-dark-1);
  color: #2A0944;
}

.header {
  padding-top: 5em;
  height: 100vh;
  background-color: var(--colorhunt-dark-4);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-evenly;
  scroll-snap-align: start;
}
.header__introduction {
  position: relative;
  flex-shrink: 1;
  flex-basis: 35%;
  flex-grow: 0;
  top: 15rem;
}
.header__title {
  font-weight: var(--weight-bold-700);
  color: var(--colorhunt-dark-1);
  font-size: 3.5rem;
}
.header__information {
  font-size: 1.5rem !important;
  color: var(--colorhunt-dark-1);
  font-weight: var(--weight-light-300) !important;
  line-height: 1.2;
  font-family: "Segoe UI", sans-serif;
  position: relative;
  left: 2rem;
}
.header__figure {
  position: relative;
  top: 2.5rem;
  width: 30rem;
  height: 30rem;
  border-radius: 30rem;
  overflow: hidden;
  z-index: 1;
  border: none;
  outline: none;
}
.header__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.container {
  overflow-y: scroll;
  height: 100vh;
  scroll-snap-type: mandatory;
  scroll-snap-points-y: repeat(100vh);
  scroll-snap-type: y mandatory;
}

.contact {
  background-color: var(--colorhunt-dark-2);
}

.project {
  height: 100vh;
  background-color: var(--colorhunt-dark-3);
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;
  padding: 0rem 1rem 1rem 1rem;
  padding-top: 4rem;
}
.project__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  border-radius: 1rem;
  background-color: var(--colorhunt-dark-4);
  height: 35rem;
  color: var(--colorhunt-dark-1);
  position: relative;
}
.project__figure {
  margin-top: 0rem;
  width: 100%;
  height: 10rem;
  overflow: hidden;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}
.project__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.project__title {
  margin-top: 0.5rem;
  font-size: 2.75rem;
  color: var(--colorhunt-dark-1);
}
.project__date {
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
}
.project__techlist {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  text-align: center;
  grid-row-gap: 1rem;
  grid-column-gap: 1rem;
}
.project__summary {
  color: var(--colorhunt-dark-text);
  line-height: 1.5;
  letter-spacing: 0.09em;
  padding: 0.1em;
  text-align: center;
}
.project__toggle {
  width: 3rem;
  height: 3rem;
  background-color: var(--colorhunt-light-1);
  border-radius: 50%;
  position: absolute;
  top: 95%;
  left: 50%;
  transform: translateX(-50%);
}
.project__external {
  margin-top: 2rem;
  padding: 1em 2.5em 1em 2.5em;
  border-radius: 1rem;
  background-color: var(--colorhunt-light-1);
  color: var(--colorhunt-dark-4);
}